body {
	font-family: $serif;
}

h1,
.h1,
h2,
.h2,
h3,
.h3,
h4,
.h4,
.h5,
h5,
.h6,
h6 {
	font-family: $serif;
	font-weight: bold;
}

a.text-dark {
	&:hover {
		text-decoration: none;
	}
}

img {
	max-width: 100%;
}

.nav-offset {
	margin-top: 56px;
}

.sticky-top-offset {
	top: 56px;
}

.nav a.nav-link svg {
	width: 18px;
	margin-top: -1px;
}

.page-content {
	font-size: 20px;
	line-height: 1.86;
	font-family: $serif;
}

.post-page {
	.post-body {
		font-size: 20px;
		line-height: 1.86;
		font-family: $serif;
		article p,
		article pre,
		article figure,
		article img,
		article blockquote,
		article iframed,
		article embed {
			margin-bottom: 2rem;
		}
	}
	.post-body blockquote {
		padding-left: 40px;
		margin-left: 0px;
		font-style: italic;
		position: relative;
	}
	.post-body blockquote:before {
		content: "â€œ";
		font-family: $serif;
		font-size: 8rem;
		margin: -1rem 2rem 0 -3.9rem;
		position: absolute;
		opacity: 1;
		float: left;
		line-height: 1;
	}
	.post-body:first-letter {
		float: left;
		font-size: 5em;
		line-height: 1;
		margin: 0 0.2em 0 0;
		vertical-align: top;
	}
}
